@import "utils";
@import "fonts";
@import "typography";

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onTab {
  @media (min-width: 768px) {
    @content;
  }
}

.page {
  margin: 0;
  font-family: Halyard Text;
  font-weight: normal;
  background: $c-gray;

  &__title {
    margin: 0;

    font-weight: bold;
    font-size: 46px;
    line-height: 105%;
    text-align: center;
    color: #1E266D;

    @include onTab {
      font-size: 56px;
    }

    &--header {
      margin-bottom: 27px;
    }

    &--main {
      font-size: 48px;
      margin-bottom: 50px;
    }

    &--portfolio {
      margin-bottom: 40px;
    }

    &--testimonials {
      margin-bottom: 56px;
    }

    &--cta {
      font-size: 36px;
      color:#fff;
      text-align: center;
      margin-bottom: 30px;

      @include onTab {
        text-align: left;
      }

      @include onDesktop {
        font-size: 48px;
        margin-bottom: 0;
      }
    }
  }

  &__nav {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transform: translateX(100%);
    transition: transform .4s;

    &:target {
      transform: translateX(0);
    }
  }
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 20px;

  @include onDesktop {
    padding: 0 40px;
  }

  &--about {
    padding: 0 20px;
    padding-top: 50px;

    @include onTab {
      padding: 0;
      padding-top: 120px;
    }
  }

  &--portfolio {
    padding-top: 60px;

    @include onTab {
      padding-top: 140px;
    }
  }

  &--testimonials {
    padding-top: 60px;

    @include onTab {
      padding-top: 120px;
    }
  }

  &--cta {
    padding: 60px 20px;

    @include onTab {
      padding-top: 88px;
      padding-bottom: 115px;
    }
  }

  &--footer {
    padding-top: 60px;

    @include onTab {
      padding-top: 117px;
    }
  }
}

.header {
  background: linear-gradient(180deg, rgba(242, 249, 255, 0.12) 0%, #F2F9FF 100%);

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__content {
    padding: 22px 0 22px;
    margin-bottom: 60px;

    @include onTab {
      margin-bottom: 146px;
    }
  }

  &__main {
    

    &-text {
      margin: 0;
      margin-bottom: 29px;

      font-size: 18px;
      line-height: 150%;
      text-align: center;
      color: #455880;
    }

    &-photo {
      display: none;
      height: 605px;

      background-image: url(../images/Graphic.png);
      background-repeat: no-repeat;
      background-position: center;

      @include onTab {
        display: block;
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 27px;

    font-weight: 500;
    font-size: 56px;
    line-height: 105%;

    text-align: center;

    color: #1E266D;
  }

  &__button {
    display: flex;
    justify-content: center;
    column-gap: 22px;

    margin-bottom: 32px;
  }
}

.menu {
  
  &__list {
    display: none;
    justify-content: space-between;
    list-style: none;
    padding: 0;
    

    @include onTab {
      display: flex;
    }
  }

  &__item {
    margin-right: 40px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: block;
    color: #1E266D;
    text-decoration: none;
  }

  &__call {
    display: none;
    margin-right: 34px;
    
    color: #1E266D;
    font-size: 16px;
    text-decoration: none;

    &--nav {
      display: block;
      font-size: 22px;
      margin-bottom: 20px;
    }

    @include onDesktop {
      display: block;
    }




    &--contact {
      color: #fff;
    }
  }

  &__contact-us {
    display: flex;
    align-items: center;
  }
}

.top-actions {
  display: flex;
  justify-content: space-between;
  // align-items: center;
}

.logo {
  display: block;

  &__image {
    height: 32px;
  }
}

.icon {
  width: 24px;
  height: 24px;

  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  @include onTab {
    display: none;
  }

  &--menu {
    background-image: url(../images/menu.svg);
  }

  &--cross {
    background-image: url(../images/cross.svg);
  }


}

.nav {
  background-color: #F0F2F6;

  &__content {
    box-sizing: border-box;
    height: 100vh;
  }

  &__menu {
    padding-top: 22px;
    margin-bottom: 48px;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    margin-bottom: 48px;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__link {
    font-weight: bold;
    font-size: 22px;
    line-height: 22px;
    text-transform: uppercase;
    color: #1E266D;
    text-decoration: none;
  }
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 113px;
  height: 40px;
  background: #3751FF;
  border-radius: 100px;

  text-decoration: none;
  color: #fff;

  &--nav {
    width: 170px;
    height: 60px;
    font-size: 22px;
  }

  &--contact {
    display: none;

    @include onTab {
      display: flex;
    }

    &:hover {
      background-color: transparent;
      border: 2px solid #3751FF;
      color: #3751FF;
    }
  }

  &--blue {
    width: 150px;
    height: 50px;

    &:hover {
      background-color: transparent;
      border: 2px solid #3751FF;
      color: #3751FF;
    }
  }

  &--white {
    width: 200px;
    height: 50px;

    color: #455880;
    background-color: #FFFFFF;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);

    &:hover {
      background-color: transparent;
      border: 2px solid #455880;
    }
  }

  &--cta {
    width: 245px;
    height: 56px;
    margin-bottom: 20px;
    background-color: #FFFFFF;

    font-weight: 500;
    font-size: 18px;
    line-height: 110%;
    color: #455880;

    &:hover {
      background-color: transparent;
      border: 2px solid #fff;
      color: #fff;
    }
  }

}

.main {
  background-color: #fff;

  &__photo {
    display: none;
    position: absolute;

    @include onDesktop {
      display: block;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 50px;

    font-size: 16px;
    line-height: 27px;
    text-align: center;
    color: rgba(69, 88, 128, 0.5);
  }

  &__logo {
    display: flex;
    justify-content: center;
    column-gap: 72px;
    row-gap: 72px;
    flex-wrap: wrap;

    margin-bottom: 162px;
  }

  &__text {
    padding-top: 78px;

    &-block {
      margin: 0 auto;
      max-width: 405px;
      min-height: 184px;
      margin-bottom: 45px;

      box-sizing: border-box;
      border-bottom: 1px solid rgba(69, 88, 128, 0.2);

      @include onDesktop {
        width: 100%;
      }

      &--last {
        margin-bottom: 70px;

        @include onTab {
          margin-bottom: 190px;
        }
      }

      &-photo {
        width: 48px;
        height: 58px;
      }

      &-title {
        display: flex;
      }

    }

    &-photo {
      width: 48px;
      height: 58px;

      margin-right: 47px;
    }

    &-title {
      display: inline-block;

      margin: 0;
      font-weight: bold;
      font-size: 22px;
      line-height: 125%;
      color: #1E266D;
    }

    &-subtitle {
      margin: 0;
      margin-left: 95px;

      font-size: 18px;
      line-height: 27px;
      color: #455880;
    }
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  @include onDesktop {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
  }

  &--desktop {
    display: block;

    @include onDesktop {
      display: grid;
    }

    &-footer {
      display: grid;
    }
  }

  &__item {
    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end + 1};
        }
      }
    }
  }
}

.portfolio {
  background-color: #FFF9EA;

  &__subtitle {
    margin: 0;
    margin-bottom: 40px;

    font-size: 21px;
    line-height: 150%;

    text-align: center;

    color: #455880;
  }

  &__button {
    display: flex;
    justify-content: center;

    margin-bottom: 37px;
  }

  &__image {
    display: flex;
    justify-content: flex-start;
    overflow: hidden;

    height: 260px;

    @include onTab {
      justify-content: center;
      height: 100%;
    }
  }
}

.testimonials {
  &__block {
    max-width:  372px;
    margin: 0 auto;
    min-height: 470px;
    padding: 0 43px;
    box-sizing: border-box;
    margin-bottom: 50px;

    border: 1px solid rgba(69, 88, 128, 0.2);
    border-radius: 4px;

    @include onDesktop {
      width: 100%;
      margin: 0;
      margin-bottom: 150px;
    }

    &-name {
      margin: 0;

      font-weight: 500;
      font-size: 22px;
      line-height: 125%;

      text-align: center;

      color: #1E266D;
    }
  }

  &__text {
    margin: 38px 0;

    font-style: normal;
    font-size: 18px;
    line-height: 180%;

    color: #455880;

    &--site {
      margin: 0;
      margin-bottom: 32px;
      text-align: center;
    }

    &--2 {
      margin-bottom: 70px;
    }

    &--3 {
      margin-bottom: 70px;
    }
  }

  &__photo {
    
    &-container {
      display: flex;
      justify-content: center;

      margin-bottom: 32px;
    }
  }
}

.cta {
  background-color: #3751FF;

  &__block {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;

    @include onTab {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__icon {
    margin-right: 20px;
  }

  &__button {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include onDesktop {
      align-items: flex-end;
    }

    &-sign {
      margin: 0;

      font-size: 16px;
      line-height: 125%;

      color: #DFE0EB;

      &-link {
        text-decoration: none;
        color: #FED385;
      }
    }
  }
}

.footer {

  &__container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    @include onDesktop {
      display: grid;
    }
  }

  &__block {
    display: none;

    @include onDesktop {
      display: block;
    }
  }

  &__logo {
    display: none;
    height: 23px;
    margin-bottom: 31px;

    @include onDesktop {
      display: block;
    }
  }

  &__text {
    display: none;
    width: 400px;
    text-align: center;
    margin: 0;

    font-size: 23px;
    line-height: 145%;
    color: #455880;

    @include onDesktop {
      display: block;
      width: 100%;
      text-align: left;
      font-size: 18px;
    }
  }

  &__column {
    display: flex;
    justify-content: space-between;

    @include onDesktop {
      display: grid;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 26px;
    line-height: 125%;
    margin-bottom: 22px;

    color: #1E266D;
    text-align: left;

    &--resources {
      font-size: 22px;
      margin-bottom: 32px;
    }
  }

  &__subtitle {
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: left;

    margin-bottom: 80px;

    @include onDesktop {
      margin-bottom: 130px;
    }
  }

  &__link {
    text-decoration: none;

    line-height: 250%;
    font-size: 22px;
    color: #455880;

    @include onDesktop {
      font-size: 18px;
    }
  }

  &--demos {
    padding-left: 0;

    @include onDesktop {
      padding-left: 20px;
    }
  }

  &--resourses {
    padding-left: 0;

    @include onDesktop {
      padding-left: 70px;
    }
  }

  &__icon {
    display: flex;
    justify-content: center;
    column-gap: 24px;

    margin-bottom: 24px;

    @include onDesktop {
      justify-content: left;
    }
  }

  &__copyright {
    display: flex;
    justify-content: space-between;
    padding-bottom: 50px;

    @include onDesktop {
      padding-bottom: 115px;
    }
    &-text {
      margin: 0;
      font-weight: normal;
      font-size: 10px;
      line-height: 27px;
      color: rgba(69, 88, 128, 0.5);

      @include onTab {
        font-size: 14px;
      }
    }
  }

  &__privacy {
    display: flex;
    column-gap: 40px;

    &-text {
      margin: 0;
      font-weight: normal;
      font-size: 10px;
      line-height: 27px;

      color: #455880;

      @include onTab {
        font-size: 14px;
      }
    }

    &-link {
      text-decoration: none;
    }
  }
}
